import { useState, useEffect } from "react";
import Link from "next/link";
import { motion } from "framer-motion";
import Navbar from "../components/Navbar";
import GameCarousel from "../components/GameCarousel";
import { supabase } from "../lib/supabaseClient";

const randomPrice = () => (Math.random() * 50 + 10).toFixed(2);

export default function Home() {
  const [heroGames, setHeroGames] = useState<any[]>([]);
  const [gamesByGenre, setGamesByGenre] = useState<{ [genre: string]: any[] }>({});
  const [emBreve, setEmBreve] = useState<any[]>([]);
  const [maisVendidos, setMaisVendidos] = useState<any[]>([]);
  const [ofertas, setOfertas] = useState<any[]>([]);
  const [gratis, setGratis] = useState<any[]>([]);
  const [recomendados, setRecomendados] = useState<any[]>([]);
  const [selectedHero, setSelectedHero] = useState(0);
  const [searchTerm, setSearchTerm] = useState("");

  const genres = ["RPG", "Indie", "Ação", "FPS", "MOBA", "Simulação"];

  useEffect(() => {
    async function fetchGames() {
      const { data, error } = await supabase.from("games").select("*");
      if (error) return console.error("Erro Supabase:", error);

      // Hero (aleatórios)
      setHeroGames(
        data.sort(() => 0.5 - Math.random()).slice(0, 5).map((g) => ({
          name: g.title,
          image: g.image,
          price: randomPrice(),
          description: g.description,
        }))
      );

      // Agrupar por gênero
      const grouped: { [genre: string]: any[] } = {};
      genres.forEach((genre) => {
        grouped[genre] = data
          .filter((g) => g.category === genre)
          .map((g) => ({
            name: g.title,
            image: g.image,
            price: randomPrice(),
            description: g.description,
          }));
      });
      setGamesByGenre(grouped);

      // Em breve
      setEmBreve(data.slice(-5).map((g) => ({
        name: g.title,
        image: g.image,
        price: randomPrice(),
        description: g.description,
      })));

      // Mais vendidos
      setMaisVendidos(data.sort(() => 0.5 - Math.random()).slice(0, 6));

      // Ofertas (simulação: aplicar desconto)
      setOfertas(
        data.slice(0, 6).map((g) => ({
          ...g,
          oldPrice: (Math.random() * 80 + 20).toFixed(2),
          discount: Math.floor(Math.random() * 50) + 10,
        }))
      );

      // Gratuitos
      setGratis(data.slice(0, 5).map((g) => ({ ...g, price: "Free" })));

      // Recomendados
      setRecomendados(data.sort(() => 0.5 - Math.random()).slice(0, 6));
    }

    fetchGames();
  }, []);
const filterGames = (games: any[]) =>
  games.filter((g) => {
    const gameName = g.name || g.title || ""; // garante que nunca é undefined
    return gameName.toLowerCase().includes(searchTerm.toLowerCase());
  });

  return (
    <div className="bg-gray-900 text-white min-h-screen">
      {/* Navbar + Search */}
      <div className="fixed top-0 left-0 w-full z-50 bg-gray-900 shadow-md">
        <Navbar />
        <div className="max-w-7xl mx-auto px-6 py-2">
          <input
            type="text"
            placeholder="Procurar jogo..."
            className="w-full p-2 rounded-lg text-black"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>

      <main className="pt-32 max-w-7xl mx-auto px-6 space-y-24">
        {/* Hero com background dinâmico */}
        {heroGames.length > 0 && (
          <section className="relative h-[550px] rounded-xl overflow-hidden shadow-lg">
            <div
              className="absolute inset-0 bg-cover bg-center blur-lg opacity-40"
              style={{ backgroundImage: `url(${heroGames[selectedHero].image})` }}
            />
            <div className="relative flex h-full">
              {/* Miniaturas */}
              <div className="flex flex-col space-y-4 w-40 p-2 z-10">
                {heroGames.map((game, i) => (
                  <img
                    key={i}
                    src={game.image}
                    alt={game.name}
                    className={`h-20 w-full object-cover rounded-lg cursor-pointer border-2 transition-all hover:scale-105 ${
                      selectedHero === i ? "border-blue-500" : "border-transparent"
                    }`}
                    onClick={() => setSelectedHero(i)}
                  />
                ))}
              </div>

              {/* Principal */}
              <div className="flex-1 relative ml-6 rounded-xl overflow-hidden shadow-lg z-10">
                <img
                  src={heroGames[selectedHero].image}
                  alt={heroGames[selectedHero].name}
                  className="w-full h-full object-cover brightness-75"
                />
                <div className="absolute bottom-8 left-8 max-w-lg">
                  <h2 className="text-4xl font-bold">{heroGames[selectedHero].name}</h2>
                  <p className="mt-2 text-lg text-gray-300">Preço: ${heroGames[selectedHero].price}</p>
                  <p className="mt-2 text-gray-300 text-sm line-clamp-3">
                    {heroGames[selectedHero].description}
                  </p>
                  <Link
                    href="/games"
                    className="mt-4 inline-block bg-blue-600 hover:bg-blue-700 px-5 py-2 rounded-lg text-lg font-semibold transition-shadow shadow-lg"
                  >
                    Jogar Agora
                  </Link>
                </div>
              </div>
            </div>
          </section>
        )}

        {/* Seções */}
        <motion.section initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 0.8 }}>
          <h2 className="text-3xl font-bold mb-4">📈 Mais Vendidos</h2>
          <GameCarousel
            title="Mais Vendidos"
            games={filterGames(maisVendidos).map((g) => ({
              name: g.title,
              image: g.image,
              badge: "Popular",
            }))}
          />
        </motion.section>

        <motion.section initial={{ y: 30, opacity: 0 }} animate={{ y: 0, opacity: 1 }}>
          <h2 className="text-3xl font-bold mb-4">💰 Ofertas Especiais</h2>
          <GameCarousel
            title="Ofertas Especiais"
            games={filterGames(ofertas).map((g) => ({
              name: g.title,
              image: g.image,
              badge: `-${g.discount}% $${g.oldPrice}`,
            }))}
          />
        </motion.section>

        {genres.map(
          (genre) =>
            gamesByGenre[genre] &&
            filterGames(gamesByGenre[genre]).length > 0 && (
              <motion.section key={genre} whileHover={{ scale: 1.02 }}>
                <h2 className="text-2xl sm:text-3xl font-bold mb-4">{genre}</h2>
                <GameCarousel
                  title={genre}
                  games={filterGames(gamesByGenre[genre]).map((g) => ({
                    name: g.name,
                    image: g.image,
                    badge: "Novo",
                  }))}
                />
              </motion.section>
            )
        )}

        {emBreve.length > 0 && (
          <section>
            <h2 className="text-3xl font-bold mb-4">⏳ Em Breve</h2>
            <GameCarousel
              title="Em Breve"
              games={filterGames(emBreve).map((g) => ({
                name: g.name,
                image: g.image,
                badge: "Exclusivo",
              }))}
            />
          </section>
        )}

        <section>
          <h2 className="text-3xl font-bold mb-4">⭐ Recomendados para Ti</h2>
          <GameCarousel
            title="Recomendados"
            games={filterGames(recomendados).map((g) => ({
              name: g.title,
              image: g.image,
              badge: "Recomendado",
            }))}
          />
        </section>

        <section>
          <h2 className="text-3xl font-bold mb-4">🎁 Jogos Gratuitos</h2>
          <GameCarousel
            title="Grátis"
            games={filterGames(gratis).map((g) => ({
              name: g.title,
              image: g.image,
              badge: "Free",
            }))}
          />
        </section>

        <section>
          <h2 className="text-3xl font-bold mb-4">🗂️ Explorar por Coleção</h2>
          <div className="grid grid-cols-2 sm:grid-cols-3 gap-6">
            {["Mundo Aberto", "Multiplayer", "História Imersiva"].map((colecao) => (
              <div
                key={colecao}
                className="relative h-40 bg-gray-800 rounded-lg flex items-center justify-center text-xl font-bold cursor-pointer hover:bg-gray-700 transition"
              >
                {colecao}
              </div>
            ))}
          </div>
        </section>
      </main>
    </div>
  );
}
