"use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import { supabase } from "../lib/supabaseClient";
import { useAuth } from "@/lib/AuthContext";

const genres = ["Ação", "Aventura", "RPG", "Indie", "Simulação", "Esportes"];

export default function Navbar() {
  const { user: authUser, logout } = useAuth();
  const supabaseUser = supabase.auth.getUser();
  
  const [games, setGames] = useState<any[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState<any[]>([]);
  const [showSearch, setShowSearch] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [user, setUser] = useState<any>(null);

  // -------------------- Sessão / Logs --------------------
  useEffect(() => {
    console.log("🔹 Navbar montada, checando sessão inicial...");
    supabase.auth.getSession().then(({ data }) => {
      console.log("🔹 Sessão inicial:", data.session);
      setUser(data.session?.user ?? null);
    });

    const { data: listener } = supabase.auth.onAuthStateChange((event, session) => {
      console.log("🔄 Auth change:", event, session);
      setUser(session?.user ?? null);
    });

    return () => listener.subscription.unsubscribe();
  }, []);

  // -------------------- Buscar jogos --------------------
  useEffect(() => {
    async function fetchGames() {
      const { data, error } = await supabase.from("games").select("id,title");
      if (error) {
        console.error("❌ Erro Supabase:", error);
        return;
      }
      console.log(`✅ Jogos carregados: ${data?.length || 0}`);
      setGames(data || []);
    }
    fetchGames();
  }, []);

  useEffect(() => {
    if (!searchTerm.trim()) {
      setSearchResults([]);
      return;
    }
    const filtered = games.filter((g) =>
      g.title?.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setSearchResults(filtered);
  }, [searchTerm, games]);

  // -------------------- Logout --------------------
  const handleLogout = async () => {
    const { error } = await supabase.auth.signOut();
    if (error) console.error("❌ Erro logout:", error);
    else console.log("✅ Logout efetuado");
    setUser(null);
  };

  return (
    <nav className="w-full bg-gray-800 p-4 shadow-md fixed top-0 left-0 z-50 flex justify-between items-center h-20">
      {/* Logo */}
      <h1 className="text-2xl sm:text-3xl font-bold text-white">
        <Link href="/">GamersClub</Link>
      </h1>

      <div className="flex items-center space-x-6 text-sm sm:text-base relative">
        {/* Explorar Dropdown */}
        <div
          className="relative"
          onMouseEnter={() => setDropdownOpen(true)}
          onMouseLeave={() => setDropdownOpen(false)}
        >
          <span className="flex items-center text-gray-300 hover:text-blue-400 cursor-pointer">
            Explorar
          </span>
          <div
            className={`absolute top-full left-0 mt-2 w-48 bg-gray-700 rounded-md shadow-lg z-50 transition-all duration-150 ${
              dropdownOpen ? "opacity-100 visible" : "opacity-0 invisible"
            }`}
          >
            {genres.map((genre, i) => (
              <Link
                key={i}
                href={`/games?genre=${genre}`}
                className="block px-4 py-2 text-white hover:bg-gray-600"
              >
                {genre}
              </Link>
            ))}
            <hr className="border-gray-600 my-1" />
            <Link href="/promotions" className="block px-4 py-2 text-white hover:bg-gray-600">
              Promoções
            </Link>
            <Link href="/novidades" className="block px-4 py-2 text-white hover:bg-gray-600">
              Novidades
            </Link>
          </div>
        </div>

        {/* Outros links */}
        <Link href="/more" className="text-gray-300 hover:text-blue-400">
          Mais
        </Link>

        {/* Pesquisa */}
        <div className="relative">
          <input
            type="text"
            placeholder="Buscar jogos..."
            className="px-3 py-1 rounded-md text-black w-32 sm:w-48 focus:outline-none"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onFocus={() => setShowSearch(true)}
            onBlur={() => setTimeout(() => setShowSearch(false), 150)}
          />
          {showSearch && searchResults.length > 0 && (
            <div className="absolute top-full left-0 w-full bg-gray-700 rounded-md shadow-lg mt-1 z-50 max-h-60 overflow-y-auto">
              {searchResults.map((g) => (
                <Link
                  key={g.id}
                  href={`/games/${g.id}`}
                  className="block px-4 py-2 text-white hover:bg-gray-600"
                >
                  {g.title}
                </Link>
              ))}
            </div>
          )}
        </div>

        {/* Conta / Login */}
        {user ? (
          <div className="relative group">
            <span className="text-gray-300 hover:text-blue-400 cursor-pointer">
              Olá, {user.user_metadata?.nickname || "Player"} 💰 $
              {user.user_metadata?.balance?.toFixed(2) || "0"}
            </span>
            <div className="absolute right-0 mt-2 w-48 bg-gray-700 rounded-md shadow-lg z-50 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
              <Link href="/profile" className="block px-4 py-2 text-white hover:bg-gray-600">
                Editar Perfil
              </Link>
              <Link href="/add-funds" className="block px-4 py-2 text-white hover:bg-gray-600">
                Adicionar Fundos
              </Link>
              <Link href="/security" className="block px-4 py-2 text-white hover:bg-gray-600">
                Segurança
              </Link>
              <button
                onClick={handleLogout}
                className="w-full text-left px-4 py-2 text-white hover:bg-gray-600"
              >
                Logout
              </button>
            </div>
          </div>
        ) : (
          <>
            <Link href="/register" className="text-gray-300 hover:text-blue-400">
              Registrar
            </Link>
            <Link href="/login" className="text-gray-300 hover:text-blue-400">
              Login
            </Link>
          </>
        )}
      </div>
    </nav>
  );
}
